<template>
  <div id="swiper">
    <div class="swiper" @touchstart="touchStart" @touchmove="touchMove" @touchend="touchEnd">
      <slot></slot>
    </div>
    <slot name="indicator">
    </slot>
    <!-- ‰∏ãÈù¢ÁöÑÂ∞èÂúÜÁÇπ -->
    <div class="indicator">
      <slot name="indicator" v-if="showIndicator && slideCount>1">
        <div v-for="index in slideCount" class="indi-item" 
        :class="{active: index === currentIndex-1}"
         :key="index"></div>
      </slot>
    </div>
  </div>
</template>

<script>
  export default {
    name: "SliderShow",
    props: {
      interval: {
        // ËΩÆÊí≠Èó¥ÈöîÊó∂Èó¥
        type: Number,
        default: 2000
      },
      animDuration: {
        type: Number,
        default: 300
      },
      moveRatio: {
        // ÁßªÂä®ËææÂà∞Â§öÂ§ßÊØî‰æã
        type: Number,
        default: 0.25
      },
      showIndicator: {
        type: Boolean,
        default: true
      }
    },
    data: function () {
      return {
        slideCount: 0, // ÂÖÉÁ¥†‰∏™Êï∞
        totalWidth: 0, // swiperÁöÑÂÆΩÂ∫¶
        swiperStyle: {}, // swiperÊ†∑Âºè
        currentIndex: 1, // ÂΩìÂâçÁöÑindex
        scrolling: false, // ÊòØÂê¶Ê≠£Âú®ÊªöÂä®
      }
    },
    mounted: function () {
      // 1.Êìç‰ΩúDOM, Âú®ÂâçÂêéÊ∑ªÂä†Slide
      setTimeout(() => {
        this.handleDom();
        // 2.ÂºÄÂêØÂÆöÊó∂Âô®
        this.startTimer();
      }, 300)
    },
    methods: {
      /**
       * ÂÆöÊó∂Âô®Êìç‰Ωú
       */
      startTimer: function () {
        this.playTimer = window.setInterval(() => {
          this.currentIndex++;
          this.scrollContent(-this.currentIndex * this.totalWidth);
        }, this.interval)
      },
      stopTimer: function () {
        window.clearInterval(this.playTimer);
      },
      /**
       * ÊªöÂä®Âà∞Ê≠£Á°ÆÁöÑ‰ΩçÁΩÆ
       */
      scrollContent: function (currentPosition) {
        // 0.ËÆæÁΩÆÊ≠£Âú®ÊªöÂä®
        this.scrolling = true;
        // 1.ÂºÄÂßãÊªöÂä®Âä®Áîª
        this.swiperStyle.transition ='transform '+ this.animDuration + 'ms';
        this.setTransform(currentPosition);
        // 2.Âà§Êñ≠ÊªöÂä®Âà∞ÁöÑ‰ΩçÁΩÆ
        this.checkPosition();
        // 4.ÊªöÂä®ÂÆåÊàê
        this.scrolling = false
      },
      /**
       * Ê†°È™åÊ≠£Á°ÆÁöÑ‰ΩçÁΩÆ
       */
      checkPosition: function () {
        window.setTimeout(() => {
          // 1.Ê†°È™åÊ≠£Á°ÆÁöÑ‰ΩçÁΩÆ
          this.swiperStyle.transition = '0ms';
          if (this.currentIndex >= this.slideCount + 1) {
            this.currentIndex = 1;
            this.setTransform(-this.currentIndex * this.totalWidth);
          } else if (this.currentIndex <= 0) {
            this.currentIndex = this.slideCount;
            this.setTransform(-this.currentIndex * this.totalWidth);
          }
          // 2.ÁªìÊùüÁßªÂä®ÂêéÁöÑÂõûË∞É
          this.$emit('transitionEnd', this.currentIndex-1);
        }, this.animDuration)
      },
      /**
       * ËÆæÁΩÆÊªöÂä®ÁöÑ‰ΩçÁΩÆ
       */
      setTransform: function (position) {
        this.swiperStyle.transform = `translate3d(${position}px, 0, 0)`;
        this.swiperStyle['-webkit-transform'] = `translate3d(${position}px), 0, 0`;
        this.swiperStyle['-ms-transform'] = `translate3d(${position}px), 0, 0`;
      },
      /**
       * Êìç‰ΩúDOM, Âú®DOMÂâçÂêéÊ∑ªÂä†Slide
       */
      handleDom: function () {
        // 1.Ëé∑ÂèñË¶ÅÊìç‰ΩúÁöÑÂÖÉÁ¥†
        let swiperEl = document.querySelector('.swiper');
        let slidesEls = swiperEl.getElementsByClassName('slider');
        // 2.‰øùÂ≠ò‰∏™Êï∞
        this.slideCount = slidesEls.length;
        // 3.Â¶ÇÊûúÂ§ß‰∫é1‰∏™, ÈÇ£‰πàÂú®ÂâçÂêéÂàÜÂà´Ê∑ªÂä†‰∏Ä‰∏™slide
        if (this.slideCount > 1) {
          let cloneFirst = slidesEls[0].cloneNode(true);
          let cloneLast = slidesEls[this.slideCount - 1].cloneNode(true);
          swiperEl.insertBefore(cloneLast, slidesEls[0]);
          swiperEl.appendChild(cloneFirst);
          //  swiperE1 ÊãøÂà∞ÁöÑÊòØÁà∂ÂÆπÂô®ÁöÑËΩÆÊí≠ÂØπË±°
          this.totalWidth = swiperEl.offsetWidth;
          this.swiperStyle = swiperEl.style;
        }
        // 4.ËÆ©swiperÂÖÉÁ¥†, ÊòæÁ§∫Á¨¨‰∏Ä‰∏™(ÁõÆÂâçÊòØÊòæÁ§∫ÂâçÈù¢Ê∑ªÂä†ÁöÑÊúÄÂêé‰∏Ä‰∏™ÂÖÉÁ¥†)
        this.setTransform(-this.totalWidth);
      },
      /**
       * ÊãñÂä®‰∫ã‰ª∂ÁöÑÂ§ÑÁêÜ
       */
      touchStart: function (e) {
        // 1.Â¶ÇÊûúÊ≠£Âú®ÊªöÂä®, ‰∏çÂèØ‰ª•ÊãñÂä®
        if (this.scrolling) return;
        // 2.ÂÅúÊ≠¢ÂÆöÊó∂Âô®
        this.stopTimer();
        // 3.‰øùÂ≠òÂºÄÂßãÊªöÂä®ÁöÑ‰ΩçÁΩÆ
        this.startX = e.touches[0].pageX;
      },
      touchMove: function (e) {
        // 1.ËÆ°ÁÆóÂá∫Áî®Êà∑ÊãñÂä®ÁöÑË∑ùÁ¶ª
        this.currentX = e.touches[0].pageX;
        this.distance = this.currentX - this.startX;
        let currentPosition = -this.currentIndex * this.totalWidth;
        let moveDistance = this.distance + currentPosition;
        // 2.ËÆæÁΩÆÂΩìÂâçÁöÑ‰ΩçÁΩÆ
        this.setTransform(moveDistance);
      },
      touchEnd: function () {
        // 1.Ëé∑ÂèñÁßªÂä®ÁöÑË∑ùÁ¶ª
        let currentMove = Math.abs(this.distance);
        // 2.Âà§Êñ≠ÊúÄÁªàÁöÑË∑ùÁ¶ª
        if (this.distance === 0) {
          return
        } else if (this.distance > 0 && currentMove > this.totalWidth * this.moveRatio) { // Âè≥ËæπÁßªÂä®Ë∂ÖËøá0.25
          this.currentIndex--
        } else if (this.distance < 0 && currentMove > this.totalWidth * this.moveRatio) { // ÂêëÂ∑¶ÁßªÂä®Ë∂ÖËøá0.25
          this.currentIndex++
        }
        // 3.ÁßªÂä®Âà∞Ê≠£Á°ÆÁöÑ‰ΩçÁΩÆ
        this.scrollContent(-this.currentIndex * this.totalWidth);
        // 4.ÁßªÂä®ÂÆåÊàêÂêéÈáçÊñ∞ÂºÄÂêØÂÆöÊó∂Âô®
        this.startTimer();
      },
      /**
       * ÊéßÂà∂‰∏ä‰∏Ä‰∏™, ‰∏ã‰∏Ä‰∏™
       */
      previous: function () {
        this.changeItem(-1);
      },
      next: function () {
        this.changeItem(1);
      },
      changeItem: function (num) {
        // 1.ÁßªÈô§ÂÆöÊó∂Âô®
        this.stopTimer();
        // 2.‰øÆÊîπindexÂíå‰ΩçÁΩÆ
        this.currentIndex += num;
        this.scrollContent(-this.currentIndex * this.totalWidth);
        // 3.Ê∑ªÂä†ÂÆöÊó∂Âô®
        this.startTimer();
      }
    }
  }
</script>

<style scoped>
  #swiper {
    overflow: hidden;
    position: relative;
  }
  .swiper {
    display: flex;
  }
  .indicator {
    display: flex;
    justify-content: center;
    position: absolute;
    width: 100%;
    bottom: 8px;
  }
  .indi-item {
    box-sizing: border-box;
    width: 8px;
    height: 8px;
    border-radius: 4px;
    background-color: #fff;
    line-height: 8px;
    text-align: center;
    font-size: 12px;
    margin: 0 5px;
  }
  .indi-item.active {
    background-color: rgba(212,62,46,1.0);
  }
</style>